<template>
  <div class='root'>
    <action-bar title='商品管理' :border="false" :left="false"></action-bar>
    <!-- 搜索 -->
    <div class="search">
      <div class="search-content" @click="handleSearch">
        <van-icon name="search" color="#b4b4b4" class="search-icon" size="16px" />
        <span>搜索</span>
      </div>
      <div class="search-batch" @click="handleBatch">

        <img src="@/assets/commoidy_edit_.png" class="small-icon" />
        <span class="batch">批量操作</span>
      </div>
    </div>
    <van-tabs @change="swiperTab" :ellipsis="false" v-model="currentTab" animated swipeable color="#42D18A">
      <van-tab :title="pageItem.title + '(' + pageItem.num + ')'" v-for="(pageItem, pageIndex) in pageData"
        :key="pageItem.title">
        <div class="goods">
          <!-- 类型 -->
          <div class="goods-type">
            <goodsType :datas="pageItem.goodsType" @clicktype="GetChoiceType"></goodsType>
          </div>
          <!-- 内容 -->
          <scroll-view :noMoreData="pageItem.noMoreData" :ref="`scroll${pageIndex}`" refresher loadmore
            @pullrefresh="onRefresh" @scrolltolower="onLoadMore" class="goods-list" :data="pageItem.goodsList">
            <goods-item v-for="(item, index) in pageItem.goodsList" :key="item.id">
              <div slot="imgSlot" class="img-slot" @click="handleCheck(index)">
                <van-checkbox v-show="pageItem.goodsCheck" checked-color="#42D18A" class="goods-check"
                  v-model="item.check" icon-size="16px"></van-checkbox>
                <van-image radius=".25rem" :src="baseUrl + item.imageUrl" class="goods-img" />
              </div>
              <div class="title" slot="nameSlot" @click="handleCheck(index)">
                <div class="status">
                  <div :class="item.state == '已下架' ? 'removed' : ''" v-if="currentTab === 0">{{ item.state }}
                  </div>
                </div>
                <div>{{ item.productName }}</div>
              </div>
              <div slot="attributeSlot" @click="handleCheck(index)">
                <div class="attribute">
                  <div class="attribute-content">售价：{{ item.price }}</div>
                  <div class="attribute-content">库存：{{ item.stock }}</div>
                </div>
                <div class="attribute">
                  <div class="attribute-content">规格：{{ item.unit }}</div>
                  <div class="attribute-content">销量：{{ item.salesVolume }}</div>
                </div>
              </div>
              <div slot="menuSlot" class="menu" v-show="!pageItem.goodsCheck">
                <template v-if="pageIndex == 4">
                  <div class="small" @click="handleReduction(item.productId)">
                    <img class="small-icon" src="@/assets/commoidy_reduction.png" />
                    恢复
                  </div>
                  <div class="small" @click="handleDeleteGs(item.productId)">
                    <img class="small-icon" src="@/assets/commoidy_delete_.png" />删除
                  </div>
                </template>
                <template v-else>
                  <div class="small" @click="handleUpdatePriceStock(item.productId, index)">
                    <img class="small-icon" src="@/assets/commoidy_price_stock.png" />价&库
                  </div>
                  <div class="small" @click="handleUpdateGoods(item.productId, index)">
                    <img class="small-icon" src="@/assets/commoidy_edit_.png" />编辑
                  </div>
                  <template v-if="item.state == '出售中'">
                    <div class="small" @click="handlePutOff(item.productId, '0', index)">
                      <img class="small-icon" src="@/assets/commoidy_offsale.png" />下架
                    </div>
                  </template>
                  <template v-else>
                    <div class="small" @click="handlePutOn(item.productId, '1', index)">
                      <img class="small-icon" src="@/assets/commoidy_onsale.png" />上架
                    </div>
                  </template>
                  <div class="small" @click="handleDeleteGoods(item.productId)">
                    <img class="small-icon" src="@/assets/commoidy_recovery.png" />
                    移除
                  </div>
                </template>
              </div>
            </goods-item>
            <div v-if="pageItem.goodsList.length === 0" class="goods-empty">
              <img src="@/assets/icon_pic_goods_empty.png" class="img-empty" />
            </div>
          </scroll-view>
        </div>
      </van-tab>
    </van-tabs>
    <!-- 批量操作界面 -->
    <div class="batch-detail" v-show="!batchHidden">
      <div class="batch-select-all">
        <van-checkbox @click="handleCheckAll" checked-color="#42D18A" class="img-slot" v-model="isCheckAll"
          icon-size="16px">全部</van-checkbox>
      </div>
      <div class="batch-operation">
        <div class="small" @click="batchUpdatePriceStock">
          <img class="small-icon" src="@/assets/commoidy_price_stock.png" />价&库
        </div>
        <template v-if="batchDown">
          <div class="small" @click="batchPutOff">
            <img class="small-icon" src="@/assets/commoidy_offsale.png" />下架
          </div>
        </template>
        <template v-if="batchUp">
          <div class="small" @click="batchPutOn">
            <img class="small-icon" src="@/assets/commoidy_onsale.png" />上架
          </div>
        </template>
        <div class="small" @click="batchDelete">
          <img class="small-icon" src="@/assets/commoidy_recovery.png" />移除
        </div>
      </div>
    </div>
  </div>
</template>
<script>

import { Icon, Tab, Tabs, Checkbox, Dialog } from 'vant'
import goodsType from '@/components/goods/GoodsType.vue'
import goodsItem from '@/components/util/GoodsItem.vue'
import { findClassify, productList, productOperate, productRemove } from '@/http/api'

export default {
  name: 'goods',
  components: {
    goodsType,
    goodsItem,
    [Icon.name]: Icon,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Checkbox.name]: Checkbox,
    [Dialog.name]: Dialog.Component
  },
  data () {
    return {
      currentTab: 0,
      pageData: [{
        onsale: '-1', // -1 全部  1 出售中  2 已售罄  0已下架
        start: 0, // 分页查询
        limit: 10, // 每页查询数量
        groupId: '', // 一级分类
        childId: '', // 二级分类
        noMoreData: false, // 没有更多数据了
        goodsList: [], // 列表数据
        isReset: true, // 是否重置页面
        goodsType: [], // 商品分类数据
        goodsTypeCheckIndex: 0, // 商品分类选中下标
        triggered: false, // 下拉刷新是否被触发
        goodsCheck: false, // 商品选择是否显示
        batchDown: true, // 是否可以批量下架
        batchUp: true, // 是否可以批量上架
        title: '全部',
        num: 0
      }, {
        onsale: '1',
        start: 0,
        limit: 10,
        groupId: '',
        childId: '',
        noMoreData: false,
        goodsList: [],
        isReset: true,
        goodsType: [],
        goodsTypeCheckIndex: 0,
        triggered: false,
        goodsCheck: false,
        batchDown: true,
        batchUp: false,
        title: '出售中',
        num: 0
      }, {
        onsale: '2',
        start: 0,
        limit: 10,
        groupId: '',
        childId: '',
        noMoreData: false,
        goodsList: [],
        isReset: true,
        goodsType: [],
        goodsTypeCheckIndex: 0,
        triggered: false,
        goodsCheck: false,
        batchDown: true,
        batchUp: false,
        title: '已售罄',
        num: 0
      }, {
        onsale: '0',
        start: 0,
        limit: 10,
        groupId: '',
        childId: '',
        noMoreData: false,
        goodsList: [],
        isReset: true,
        goodsType: [],
        goodsTypeCheckIndex: 0,
        triggered: false,
        goodsCheck: false,
        batchDown: false,
        batchUp: true,
        title: '已下架',
        num: 0
      }, {
        onsale: '3',
        start: 0,
        limit: 10,
        groupId: '',
        childId: '',
        noMoreData: false,
        goodsList: [],
        isReset: true,
        goodsType: [],
        goodsTypeCheckIndex: 0,
        triggered: false,
        goodsCheck: false,
        batchDown: false,
        batchUp: true,
        title: '回收站',
        num: 0
      }],
      batchHidden: true, // 是否隐藏批量操作
      isCheckAll: false, // 全选是否选中
      batchDown: false, // 是否显示批量下架
      batchUp: false // 是否显示批量上架
    }
  },
  beforeRouteEnter (to, from, next) {
    next()
    const goodsData = from.params.goodsData
    if (goodsData) {
      next(vm => {
        const pageData = vm.pageData
        const currentTab = vm.currentTab
        for (let index = 0; index < pageData.length; index++) {
          const page = pageData[index]
          if (index === currentTab) {
            for (const updateGs of goodsData) {
              const goodsItem = page.goodsList[updateGs.index]
              if (goodsItem != null && goodsItem !== undefined) {
                goodsItem.price = updateGs.price
                goodsItem.stock = updateGs.stock
                goodsItem.check = false
              }
            }
          } else {
            page.isReset = true
          }
        }
        vm.pageData = pageData
      })
    }
  },
  methods: {
    // 批量删除
    batchDelete () {
      const data = this.batchCheck()
      if (data) {
        const ids = this.getCheckids(data)
        const that = this
        Dialog.confirm({
          title: '温馨提示',
          message: '确定将选中的商品移入回收站吗？',
          confirmButtonText: '确定',
          confirmButtonColor: '#E81A2E'
        })
          .then(() => {
            // on confirm
            that.requestDelete(ids, 1)
          })
          .catch(() => {
            // on cancel
          })
      }
    },
    // state：0：在售，1：回收站，2、删除
    requestDelete (productIds, state) {
      productRemove({
        productIds,
        state
      }).then(res => {
        this.clearDataRefresh()
        let msg = null
        if (state === '0' || state === 0) {
          msg = '恢复成功'
        } else if (state === '1' || state === 1) {
          msg = '移除成功'
        } else if (state === '2' || state === 2) {
          msg = '删除成功'
        }
        setTimeout(() => {
          this.$toast.success(msg)
        }, 150)
      }).catch(res => {
        this.$toast(res.msg)
      })
    },
    // 批量上架
    batchPutOn () {
      const data = this.batchCheck()
      if (data) {
        this.goodsOffOnConfirm(this.getCheckids(data), '1', '温馨提示', '确定将选中的商品上架吗？', '#42D18A')
      }
    },
    // 批量下架
    batchPutOff () {
      const data = this.batchCheck()
      if (data) {
        this.goodsOffOnConfirm(this.getCheckids(data), '0', '温馨提示', '确定将选中的商品下架吗？', '#FF0000')
      }
    },
    goodsOffOnConfirm (id, onsale, title, content, confirmColor, onsaleStatus, index) {
      const that = this
      Dialog.confirm({
        title: title,
        message: content,
        confirmButtonText: '确定',
        confirmButtonColor: confirmColor
      })
        .then(() => {
          // on confirm
          that.requestProductOperate(id, onsale, onsaleStatus, index)
        })
        .catch(() => {
          // on cancel
        })
    },
    // 上下架接口请求
    requestProductOperate (productIds, onsale, onsaleStatus = -1, index = -1) {
      productOperate({
        productIds,
        onsale
      }).then(res => {
        if (onsaleStatus === -1) {
          this.clearDataRefresh()
        } else if (this.currentTab === 1 || this.currentTab === 3) {
          this.localOperate(onsaleStatus, index)
        } else {
          this.clearDataRefresh()
        }
        setTimeout(() => {
          this.$toast.success('操作成功')
        }, 150)
      }).catch(res => {
        this.$toast(res.msg)
      })
    },
    // 本地修改上下架状态
    localOperate (onsaleStatus, index) {
      // console.log('onsaleStatus', onsaleStatus)
      // console.log('index', index)
      const pageData = this.pageData
      const currentTab = this.currentTab
      for (let i = 0; i < pageData.length; i++) {
        const page = pageData[i]
        if (currentTab !== i) {
          page.isReset = true
        } else {
          const stock = page.goodsList[index].stock
          page.goodsList.splice(index, 1)
          const tab = pageData[currentTab]
          tab.num = tab.num - 1
          if (onsaleStatus === 0) {
            // 下架
            const tabOff = pageData[3]
            tabOff.num = tabOff.num + 1
          } else {
            if (stock === 0) {
              const tabUp = pageData[2]
              tabUp.num = tabUp.num + 1
            } else {
              const tabUp = pageData[1]
              tabUp.num = tabUp.num + 1
            }
          }
        }
      }
      this.pageData = pageData
      this.$forceUpdate()
    },
    // 清除所有列表数据并刷新当前页
    clearDataRefresh () {
      const pageData = this.pageData
      for (const page of pageData) {
        page.isReset = true
      }
      this.pageData = pageData
      this.getGoodsData(false)
    },
    getCheckids (data) {
      let ids = ''
      for (let i = 0; i < data.length; i++) {
        const elt = data[i]
        ids += elt.productId
        if (i < data.length - 1) {
          ids += ','
        }
      }
      return ids
    },
    handleUpdatePriceStock (productId, index) {
      this.$router.push({
        path: '/updatePriceStock',
        query: {
          data: [{
            productId,
            index
          }]
        }
      })
    },
    // 上架
    handlePutOn (id, onsale, index) {
      this.goodsOffOnConfirm(id, onsale, '温馨提示', '确定将商品上架吗？', '#42D18A', onsale, index)
    },
    // 跳转编辑界面
    handleUpdateGoods (id, index) {
      this.$router.push({
        path: '/editAttribute',
        query: {
          id,
          index
        }
      })
    },
    // 下架
    handlePutOff (id, onsale, index) {
      this.goodsOffOnConfirm(id, onsale, '温馨提示', '确定将商品下架吗？', '#FF0000', onsale, index)
    },
    // 还原
    handleReduction (id) {
      const that = this
      Dialog.confirm({
        title: '温馨提示',
        message: '确定恢复该商品吗？',
        confirmButtonText: '确定',
        confirmButtonColor: '#42D18A'
      })
        .then(() => {
          // on confirm
          that.requestDelete(id, 0)
        })
        .catch(() => {
          // on cancel
        })
    },
    handleDeleteGs (id) {
      const that = this
      Dialog.confirm({
        title: '温馨提示',
        message: '确定要永久删除该商品吗？',
        confirmButtonText: '确定',
        confirmButtonColor: '#E81A2E'
      })
        .then(() => {
          // on confirm
          that.requestDelete(id, 2)
        })
        .catch(() => {
          // on cancel
        })
    },
    // 移除商品
    handleDeleteGoods (id) {
      const that = this
      Dialog.confirm({
        title: '温馨提示',
        message: '确定要将该商品移入回收站吗？',
        confirmButtonText: '确定',
        confirmButtonColor: '#E81A2E'
      })
        .then(() => {
          // on confirm
          that.requestDelete(id, 1)
        })
        .catch(() => {
          // on cancel
        })
    },
    GetChoiceType (data) {
      const cData = this.pageData[this.currentTab]
      const goodsType = cData.goodsType
      const parentIndex = data.parentIndex
      const childIndex = data.childIndex

      cData.goodsTypeCheckIndex = parentIndex
      cData.groupId = goodsType[parentIndex].categoryId
      cData.childId =
        childIndex >= 0
          ? goodsType[parentIndex].children[childIndex].category2Id
          : ''
      cData.goodsList = []
      cData.start = 0
      this.getGoodsData()
    },
    // 单选商品
    handleCheck (index) {
      if (this.batchHidden === true) {
        return
      }
      const pageData = this.pageData
      const goods = pageData[this.currentTab].goodsList[index]
      if (goods.check === undefined || !goods.check) {
        goods.check = true
      } else {
        goods.check = false
      }
      this.pageData = pageData
      this.$forceUpdate()
    },
    batchCheck () {
      const goodsList = this.pageData[this.currentTab].goodsList
      const data = []
      if (goodsList) {
        for (let i = 0; i < goodsList.length; i++) {
          const goods = goodsList[i]
          if (goods.check) {
            data.push({
              productId: goods.productId,
              index: i
            })
          }
        }
      }
      if (data.length === 0) {
        this.$toast('请选择商品')
        return
      }
      return data
    },
    // 批量修改价格、库存
    batchUpdatePriceStock () {
      const data = this.batchCheck()
      if (data) {
        console.log(data)
        this.$router.push({
          path: '/updatePriceStock',
          query: {
            data
          }
        })
      }
    },
    // 批量全选，只选中当前tab页面已加载的数据
    handleCheckAll () {
      const isCheckAll = this.isCheckAll
      const pageData = this.pageData
      const goodsList = pageData[this.currentTab].goodsList
      if (!goodsList) {
        return
      }
      goodsList.forEach(elt => {
        elt.check = isCheckAll
      })
      this.pageData = pageData
      this.$forceUpdate()
    },
    handleSearch () {
      this.$router.push('/goodsSearch')
    },
    // 批量修改栏目是否显示
    handleBatch () {
      const pageIndex = this.currentTab
      if (pageIndex === 4) {
        this.$toast('回收站不可批量操作')
        return
      }
      this.batchHidden = !this.batchHidden
      const batchHidden = this.batchHidden
      const pageData = this.pageData
      const cData = pageData[this.currentTab]
      cData.goodsCheck = !batchHidden
      // 控制是否显示上下架按钮
      this.isCheckAll = false
      this.pageData = pageData
      this.batchDown = cData.batchDown
      this.batchUp = cData.batchUp
    },
    // 滑动切换
    swiperTab () {
      // 切换tab
      this.switchReduction()
      const page = this.pageData[this.currentTab]
      if (page.isReset) {
        this.getGoodsData()
      }
    },
    // 切换页布局属性还原
    switchReduction () {
      const pageData = this.pageData
      pageData.forEach(elt => {
        elt.goodsCheck = false
      })
      this.isCheckAll = false
      this.batchHidden = true
      this.pageData = pageData
    },
    // 上拉加载更多
    onLoadMore () {
      const pageData = this.pageData
      const cData = pageData[this.currentTab]
      if (cData.noMoreData === true) {
        return
      }
      cData.start += cData.limit
      this.pageData = pageData
      this.getGoodsData()
    },
    onRefresh () {
      // 如果goodsType未获取过，则获取先获取
      const pageData = this.pageData
      const cData = pageData[this.currentTab]
      if (cData.goodsType == null || cData.goodsType.length === 0) {
        this.getGoodsType()
        return
      }
      cData.isReset = true
      this.pageData = pageData
      this.getGoodsData()
    },
    /***
       * 获取商品列表
       */
    getGoodsData (showDialog = true) {
      const pageData = this.pageData
      const cData = pageData[this.currentTab]
      if (cData.isReset) {
        cData.start = 0
      }
      productList({
        showDialog,
        start: cData.start,
        limit: cData.limit,
        onsale: cData.onsale,
        categoryId: (cData.childId !== '') ? cData.childId : cData.groupId
      }).then(res => {
        // 装载数据
        let nmd = false
        if (res.data.result.length < 10) {
          nmd = true
        }
        cData.noMoreData = nmd
        if (cData.goodsList == null || cData.isReset === true) {
          cData.goodsList = res.data.result
        } else {
          cData.goodsList.push(...res.data.result)
        }
        cData.isReset = false

        this.isCheckAll = false
        // tab数量
        for (let index = 0; index < pageData.length; index++) {
          const tab = pageData[index]
          if (index === 0 && res.data.allCount !== -1) {
            tab.num = res.data.allCount
          } else if (index === 1 && res.data.salesCount !== -1) {
            tab.num = res.data.salesCount
          } else if (index === 2 && res.data.saleOutCount !== -1) {
            tab.num = res.data.saleOutCount
          } else if (index === 3 && res.data.lowCount !== -1) {
            tab.num = res.data.lowCount
          } else if (index === 4 && res.data.recoveryCount !== -1) {
            tab.num = res.data.recoveryCount
          }
        }
        this.pageData = pageData
        this.scrollFinally()
      }).catch(rej => {
        this.scrollFinally()
        this.$toast(rej.msg)
      })
    },
    scrollFinally () {
      const scroll = this.$refs[`scroll${this.currentTab}`][0]
      scroll.refreshed()
      scroll.loaded()
    },
    // 获取分类数据
    getGoodsType () {
      findClassify().then(res => {
        const pageData = this.pageData
        const currentTab = this.currentTab
        const currentData = pageData[currentTab]
        // 目前商品类型数据都一样
        for (const page of pageData) {
          page.goodsType = res.data
        }
        this.pageData = pageData
        // 根据设置的下标获取其他信息
        if (currentData.goodsType.length > 0) {
          currentData.groupId = currentData.goodsType[currentData.goodsTypeCheckIndex].categoryId
          this.pageData = pageData
          this.getGoodsData()
        }
      }).catch(res => {
        this.$toast(res.msg)
      })
    }
  },
  mounted () {
    this.getGoodsType()
  }
}
</script>
<style lang="scss" scoped>
@import '@/utils/tabScroll.css';

.root {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding-bottom: 3.125rem;
}

.container {
  flex-grow: 1;
  position: relative;
}

.content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.search {
  height: 1.875rem;
  padding-left: .6875rem;
  display: flex;
  align-items: center;
}

.search-content {
  display: flex;
  flex-direction: row;
  background-color: #F2F2F2;
  border-radius: .8125rem;
  flex-grow: 1;
  height: 1.25rem;
  padding: .3125rem;
  justify-content: center;
  align-items: center;
  color: #b4b4b4;
  font-size: .75rem;
}

.search-icon {
  margin-right: .1875rem;
}

.search-batch {
  height: 100%;
  padding-left: .875rem;
  padding-right: .875rem;
  display: flex;
