<template>
  <scroll-view class="scroll-type" :data="datas" >
    <template v-for="(item, pIndex) in datas">
      <div
        @click="goodsTypeClick(pIndex)"
        :key="item.categoryId"
        :class="{
          goods: true,
          'goods-type-item': true,
          'goods-type-checked': goodsTypeCheckIndex == pIndex,
          'goods-type-child-checked':
            goodsTypeCheckIndex === pIndex && goodsTypeChildCheckIndex != -1,
        }"
      >
        {{ item.categoryName }}
        <!-- 子类型 -->
      </div>
      <template v-if="goodsTypeCheckIndex === pIndex ? true : false">
        <template v-for="(cItem, cIndex) in item.children">
        <div
          :key="cItem.category2Id"
          :class="[
            'goods-type-item',
            'item-child',
            goodsTypeChildCheckIndex == cIndex ? 'goods-type-checked' : '',
          ]"
          @click="goodsTypeChildClick(cIndex)"
        >
          {{ cItem.category2Name }}
        </div>
      </template>
      </template>
    </template>
  </scroll-view>
</template>
<script>
export default {
  name: 'goodsType',
  props: {
    datas: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      goodsTypeChildCheckIndex: -1,
      goodsTypeCheckIndex: 0
    }
  },
  methods: {
    // 点击商品类型
    goodsTypeClick (index) {
      if (this.goodsTypeCheckIndex === index &&
        this.goodsTypeChildCheckIndex === -1) {
        return
      }
      this.typeClick(index)
    },
    typeClick (index) {
      this.goodsTypeCheckIndex = index
      this.goodsTypeChildCheckIndex = -1
      this.$emit('clicktype', {
        parentIndex: this.goodsTypeCheckIndex,
        childIndex: this.goodsTypeChildCheckIndex
      })
    },
    // 点击商品类型子项
    goodsTypeChildClick (cIndex) {
      if (this.goodsTypeChildCheckIndex === cIndex) {
        return
      }
      this.goodsTypeChildCheckIndex = cIndex
      this.$emit('clicktype', {
        parentIndex: this.goodsTypeCheckIndex,
        childIndex: this.goodsTypeChildCheckIndex
      })
    }
  },
  mounted () {}
}
</script>
<style lang="scss" scoped>
.goods-type-item {
  padding: 0.25rem;
  min-height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.goods {
  background-color: transparent;
  color: #666666;
  background-color: #f5f7f9;
  font-size: 0.875rem;
}

.scroll-type {
  width: 100%;
  height: 100%;
  background-color: #f5f7f9;
}

.goods-type-checked {
  color: #42d18a;
  background-color: white;
}

.goods-type-child-checked {
  color: #42d18a;
  background-color: transparent;
}

.item-child {
  font-size: 0.75rem;
}
</style>
